.goto-bar {
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  height: 3.5rem;
  width: calc(100% - 9rem);
  margin: 0 4.5rem;
  z-index: 11;
  bottom: 1rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -ms-flex-direction: row;
      flex-direction: row;
  background-color: #FFF;
}

  .bar-heading {
    border-top-left-radius: 0.5rem;
    border-bottom-left-radius: 0.5rem;
    padding: 1.25rem 0;
    width: 16%;
    text-align: center;
    background-color: #E3E3E3;
    text-transform: capitalize;
  }

  .bar-input {
    border-left: 0.15rem solid #E3E3E3;
    border-top: 0.15rem solid #E3E3E3;
    border-bottom: 0.15rem solid #E3E3E3;
    border-right: 0;
    display: block;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: 3.5rem;
    padding: 0 1rem;
    font: 400 1rem "Poppins";
    outline: none;
    letter-spacing: 0.3rem;
  }
    .bar-input::-webkit-input-placeholder {
      letter-spacing: initial;
      color: #333;
    }
    .bar-input:-ms-input-placeholder {
      letter-spacing: initial;
      color: #333;
    }
    .bar-input::-ms-input-placeholder {
      letter-spacing: initial;
      color: #333;
    }
    .bar-input::placeholder {
      letter-spacing: initial;
      color: #333;
    }
  .bar-input.date, .bar-input.month {
    width: 10%;
  }
  .bar-input.year {
    width: 14%;
  }

  .bar-submit {
    width: 10%;
    height: 3.5rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    text-align: center;
    background-color: rgba(255, 0, 0, 0.3);
    cursor: pointer;
    border: none;
    font: 400 1.5rem "Poppins";
    color: #111;
    border-top-right-radius: 0.5rem;
    border-bottom-right-radius: 0.5rem;
    opacity: 0.75;
    outline: none;
  }
    .bar-submit:hover, .bar-submit:focus {
      opacity: 1;
    }

  .bar-today {
    display: -ms-inline-flexbox;
    display: inline-flex;
    width: 40%;
    border-top: 0.15rem solid #E3E3E3;
    border-bottom: 0.15rem solid #E3E3E3;
    border-left: 0;
    border-right: 0;
    height: 3.5rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    cursor: pointer;
    padding: 0.6rem 1rem;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: start;
        justify-content: flex-start;
  }
    .bar-today:hover {
      background-color: #F2F2F2;
    }

    .today-heading {
      font-size: 0.9rem;
      font-weight: 600;
      margin-right: 1rem;
    }

    .today-date {
      text-transform: capitalize;
      font-size: 2rem;
      font-weight: 200;
    }



